@import "../../style.module";

.card {
  @include flex-column;
  align-items: flex-start;
  justify-content: space-between;
  width: 310px;
  height: 330px;
  color: $main-black;
  background-color: $main-white;
  border-radius: $border-radius;
  border: 2px solid $main-gray;
  overflow: hidden;
  text-decoration: none;
  transition: 0.5s all;

  &:hover {
    background-color: $catalog-page-color;
    border-color: $catalog-page-color;
    color: $main-white;
  }

  &__wrapperImg {
    @include flex-column;
    width: 100%;
    height: 130px;
    background-color: $main-white;
    border-bottom-left-radius: $border-radius;
    border-bottom-right-radius: $border-radius;

    &__image {
      height: 100%;
      border-bottom-left-radius: $border-radius;
      border-bottom-right-radius: $border-radius;
      align-self: center;
    }
  }


  &__wrapper {
    @include flex-column;
    align-items: flex-start;
    gap: 15px;
    width: 100%;
    padding: 10px 20px 20px;

    &__name {
      @include inter-regular-20;
      font-size: 25px;
      height: 90px;
      display: -webkit-box; /* Ð˜ÑÐ¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ð½Ð¸Ðµ flex-ÐºÐ¾Ð½Ñ‚ÐµÐ¹Ð½ÐµÑ€Ð° Ñ Ð¾Ð¿Ñ€ÐµÐ´ÐµÐ»ÐµÐ½Ð½Ð¾Ð¹ Ð¾Ñ€Ð¸ÐµÐ½Ñ‚Ð°Ñ†Ð¸ÐµÐ¹ */
      -webkit-box-orient: vertical; /* ÐžÐ¿Ñ€ÐµÐ´ÐµÐ»ÐµÐ½Ð¸Ðµ Ð²ÐµÑ€Ñ‚Ð¸ÐºÐ°Ð»ÑŒÐ½Ð¾Ð¹ Ð¾Ñ€Ð¸ÐµÐ½Ñ‚Ð°Ñ†Ð¸Ð¸ ÐºÐ¾Ð½Ñ‚ÐµÐ¹Ð½ÐµÑ€Ð° */
      -webkit-line-clamp: 3; /* ÐžÐ¿Ñ€ÐµÐ´ÐµÐ»ÐµÐ½Ð¸Ðµ Ð¼Ð°ÐºÑÐ¸Ð¼Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ ÐºÐ¾Ð»Ð¸Ñ‡ÐµÑÑ‚Ð²Ð° Ð¾Ñ‚Ð¾Ð±Ñ€Ð°Ð¶Ð°ÐµÐ¼Ñ‹Ñ… ÑÑ‚Ñ€Ð¾Ðº */
      overflow: hidden; /* ÐžÐ±Ñ€ÐµÐ·Ð°Ð½Ð¸Ðµ Ñ‚ÐµÐºÑÑ‚Ð°, ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¹ Ð½Ðµ Ð¿Ð¾Ð¼ÐµÑ‰Ð°ÐµÑ‚ÑÑ Ð² Ð·Ð°Ð´Ð°Ð½Ð½Ð¾Ðµ ÐºÐ¾Ð»Ð¸Ñ‡ÐµÑÑ‚Ð²Ð¾ ÑÑ‚Ñ€Ð¾Ðº */
      text-overflow: ellipsis;
    }

    &__tags {
      @include flex-row;
      flex-wrap: wrap;
      justify-content: flex-start;
      align-items: flex-start;
      gap: $indent-10;
      width: 100%;
    }
  }
}

@media screen and (max-width: 485px) {
  .card__wrapper__name {
    @include inter-regular-20;
  }
}

@media screen and (max-width: 330px) {
  .card {
    width: 300px;
  }
}