@import "../../style.module";

.wrapper {
  @include flex-column;
  justify-content: center;
  gap: 20px;
  position: relative;
  width: 420px;
  height: 345px;
  color: $main-black;
  background-color: $main-white;
  border-radius: $border-radius;
  transition: 0.3s all;
  text-decoration: none;

  &:hover {
    background-color: $news-page-color;
  }

  &:hover p, &:hover h4 {
    color: $main-white;
  }

  &__image {
    background-color: $main-white;
    max-width: 420px;
    max-height: 200px;
    width: 100%;
    height: 100%;
    border-radius: $border-radius;
  }

  &__textBlock {
    @include flex-column;
    align-items: flex-start;
    justify-content: space-between;
    color: $main-black;
    padding: 0 20px 20px;
    width: 100%;
    height: 125px;
    //gap: 50px;
    text-decoration: none;

    &__title {
      @include inter-regular-20;
      width: 100%;
      display: -webkit-box; /* Ð˜ÑÐ¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ð½Ð¸Ðµ flex-ÐºÐ¾Ð½Ñ‚ÐµÐ¹Ð½ÐµÑ€Ð° Ñ Ð¾Ð¿Ñ€ÐµÐ´ÐµÐ»ÐµÐ½Ð½Ð¾Ð¹ Ð¾Ñ€Ð¸ÐµÐ½Ñ‚Ð°Ñ†Ð¸ÐµÐ¹ */
      -webkit-box-orient: vertical; /* ÐžÐ¿Ñ€ÐµÐ´ÐµÐ»ÐµÐ½Ð¸Ðµ Ð²ÐµÑ€Ñ‚Ð¸ÐºÐ°Ð»ÑŒÐ½Ð¾Ð¹ Ð¾Ñ€Ð¸ÐµÐ½Ñ‚Ð°Ñ†Ð¸Ð¸ ÐºÐ¾Ð½Ñ‚ÐµÐ¹Ð½ÐµÑ€Ð° */
      -webkit-line-clamp: 2; /* ÐžÐ¿Ñ€ÐµÐ´ÐµÐ»ÐµÐ½Ð¸Ðµ Ð¼Ð°ÐºÑÐ¸Ð¼Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ ÐºÐ¾Ð»Ð¸Ñ‡ÐµÑÑ‚Ð²Ð° Ð¾Ñ‚Ð¾Ð±Ñ€Ð°Ð¶Ð°ÐµÐ¼Ñ‹Ñ… ÑÑ‚Ñ€Ð¾Ðº */
      overflow: hidden; /* ÐžÐ±Ñ€ÐµÐ·Ð°Ð½Ð¸Ðµ Ñ‚ÐµÐºÑÑ‚Ð°, ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¹ Ð½Ðµ Ð¿Ð¾Ð¼ÐµÑ‰Ð°ÐµÑ‚ÑÑ Ð² Ð·Ð°Ð´Ð°Ð½Ð½Ð¾Ðµ ÐºÐ¾Ð»Ð¸Ñ‡ÐµÑÑ‚Ð²Ð¾ ÑÑ‚Ñ€Ð¾Ðº */
      text-overflow: ellipsis;
    }

    &__button {
      @include flex-row;
      justify-content: flex-start;
      gap: $indent-10;
      width: 100%;

      &__p {
        @include inter-medium-14;
      }
    }
  }
}

@media screen and (max-width: 1415px) {
  .wrapper {
    width: 400px;
  }
}

@media screen and (max-width: 1360px) {
  .wrapper {
    width: 310px;
    border: 2px solid $main-gray;
  }
  .wrapper__image {
    width: 310px;
  }
}

@media screen and (max-width: 485px) {
  .wrapper__textBlock__title {
    @include inter-regular-18;
  }
}

@media screen and (max-width: 330px) {
  .wrapper, .wrapper__image {
    width: 300px;
  }
}